<template>
  <div name="FocusMain" class="FocusMain-wrap">
    <el-row class="title" type="flex" justify="space-between">
      <el-col>
        <font v-if="isMyCenter" @click="push(1)" class="behovered"
          >我的关注</font
        >
        <font v-if="!isMyCenter" @click="push(1)" class="behovered"
          >ta的关注</font
        >
      </el-col>
      <el-col>
        <font v-if="isMyCenter" @click="push(2)" class="behovered beclicked"
          >我的粉丝</font
        >
        <font v-if="!isMyCenter" @click="push(2)" class="behovered beclicked"
          >ta的粉丝</font
        >
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row type="flex" justify="center">
      <el-col :span="0.5">
        <div v-for="index in 8" :key="index" style="margin-bottom: 10px">
          <focusItemVue />
          <el-divider></el-divider>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <div class="noList" v-if="emptyList">
        <font v-if="isMyCenter">您还没有粉丝哦......</font>
        <font v-if="!isMyCenter">ta还没有粉丝哦......</font>
      </div>
    </el-row>
  </div>
</template>

<script>
import focusItemVue from "./focus-item.vue";
export default {
  name: "FocusMain",
  data() {
    return {
      isMyCenter: true,
      user_list: [],
    };
  },
  computed: {
    listnum() {
      return 8;
    },
    emptyList() {
      if (this.listnum == 0) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    push(num) {
      let route_name;
      if (num == 1) {
        route_name = "MyFocusList";
      }
      if (num == 2) {
        route_name = "MyFansList";
      }
      this.$router.push({
        name: route_name,
      });
    },
  },
  components: { focusItemVue },
};
</script>

<style scoped>
.FocusMain-wrap {
  position: relative;
  width: 100%;
  border-radius: 10px;
  background: #ffffff;
  padding-bottom: 20px;
}
.title {
  width: 400px;
  padding: 20px 0 20px 100px;
}
.title > * {
  height: 20px;
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
  cursor: pointer;
}
ul {
  position: relative;
  top: 50px;
  width: 820px;
  list-style: none;
}
.noList {
  width: 600px;
  height: 100px;
  background-color: #eeeeee;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.noList font {
  /* width: 400px; */
  height: 18px;
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 20px;
  letter-spacing: 0em;
  color: #3d3d3d;
}
</style>